import { refs } from './refs';
import { onCloseModal, onNextImgGallery } from './close-modal';

// Ð ÐµÐ°Ð»Ð¸Ð·Ð°Ñ†Ð¸Ñ Ð´ÐµÐ»ÐµÐ³Ð¸Ñ€Ð¾Ð²Ð°Ð½Ð¸Ñ Ð½Ð° Ð³Ð°Ð»ÐµÑ€ÐµÐµ ul.js-gallery Ð¸ Ð¿Ð¾Ð»ÑƒÑ‡ÐµÐ½Ð¸Ðµ url Ð±Ð¾Ð»ÑŒÑˆÐ¾Ð³Ð¾ Ð¸Ð·Ð¾Ð±Ñ€Ð°Ð¶ÐµÐ½Ð¸Ñ.

function openGallery(event) {
  event.preventDefault(); //ÐŸÐ¾Ð´Ð¼ÐµÐ½Ð° Ð¿ÐµÑ€ÐµÑ…Ð¾Ð´Ð° Ð¿Ð¾ ÑÑÑ‹Ð»ÐºÐµ

  if (event.target.nodeName !== 'IMG') {
    return;
  }

  const imageRef = event.target;

  const largeImageURL = imageRef.dataset.source;

  refs.largeImage.src = largeImageURL; // ÐŸÐ¾Ð´Ð¼ÐµÐ½Ð° Ð·Ð½Ð°Ñ‡ÐµÐ½Ð¸Ñ Ð°Ñ‚Ñ€Ð¸Ð±ÑƒÑ‚Ð° src ÑÐ»ÐµÐ¼ÐµÐ½Ñ‚Ð° img.lightbox__image.
  refs.largeImage.alt = imageRef.getAttribute('alt');

  // console.log(largeImageURL);
  refs.modal.classList.add('is-open'); // ÐžÑ‚ÐºÑ€Ñ‹Ñ‚Ð¸Ðµ Ð¼Ð¾Ð´Ð°Ð»ÑŒÐ½Ð¾Ð³Ð¾ Ð¾ÐºÐ½Ð° Ð¿Ð¾ ÐºÐ»Ð¸ÐºÑƒ Ð½Ð° ÑÐ»ÐµÐ¼ÐµÐ½Ñ‚Ðµ Ð³Ð°Ð»ÐµÑ€ÐµÐ¸.
  window.addEventListener('keydown', onPressEscape);
  window.addEventListener('keydown', onNextImgGallery);

  setLargeImageSrc(largeImageURL);
}

//ÐŸÐ¾Ð´Ð¼ÐµÐ½Ð° Ð·Ð½Ð°Ñ‡ÐµÐ½Ð¸Ñ Ð°Ñ‚Ñ€Ð¸Ð±ÑƒÑ‚Ð° src ÑÐ»ÐµÐ¼ÐµÐ½Ñ‚Ð° img.lightbox__image.

function setLargeImageSrc(url) {
  refs.largeImage.src = url;
}

// Ð·Ð°ÐºÑ€Ñ‹Ñ‚Ð¸Ðµ Ð¿Ð¾ ÐºÐ»Ð¸ÐºÑƒ Ð½Ð° ESC
function onPressEscape(event) {
  if (event.code === 'Escape') {
    onCloseModal();
  }
}

export { openGallery, setLargeImageSrc, onPressEscape };
